body {
    margin: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background: linear-gradient(to right, #f4f7fc, #eaf0f7);
    color: #222;
  }
  
  .app-container {
    padding: 40px 20px;
    display: flex;
    justify-content: center;
  }
  
  .main-box {
    max-width: 900px;
    width: 100%;
    background-color: #ffffffcc;
    border-radius: 12px;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
    padding: 30px;
    backdrop-filter: blur(6px);
  }
  
  .main-title {
    text-align: center;
    font-size: 2.5em;
    margin-bottom: 5px;
    color: #003366;
  }
  
  .subtitle {
    text-align: center;
    font-size: 1.2em;
    margin-bottom: 30px;
    color: #555;
  }
  
  .search-box {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-bottom: 30px;
    justify-content: center;
  }
  
  .search-input, .dropdown, .search-button {
    padding: 10px 15px;
    font-size: 16px;
    border-radius: 6px;
    border: 1px solid #ccc;
    outline: none;
  }
  
  .search-input {
    flex: 1 1 200px;
    min-width: 200px;
  }
  
  .dropdown {
    min-width: 160px;
  }
  
  .search-button {
    background-color: #003366;
    color: white;
    border: none;
    cursor: pointer;
    transition: background 0.3s ease;
  }
  
  .search-button:hover:not(:disabled) {
    background-color: #00509e;
  }
  
  .search-button:disabled {
    background-color: #7f8fa6;
    cursor: not-allowed;
  }
  
  .result-block {
    margin-top: 30px;
  }
  
  .section-title {
    font-size: 1.5em;
    margin-bottom: 15px;
    color: #003366;
  }
  
  .card-grid {
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }
  
  .card {
    background: white;
    border: 1px solid #ccc;
    padding: 20px;
    border-radius: 10px;
    transition: box-shadow 0.3s ease;
    cursor: pointer;
  }
  
  .card:hover {
    box-shadow: 0 8px 18px rgba(0, 0, 0, 0.12);
  }
  
  .card-title {
    font-size: 1.2em;
    color: #00509e;
    margin-bottom: 10px;
  }
  .no-results {
    margin-top: 20px;
    color: #a00;
    font-weight: 500;
    text-align: center;
    font-size: 1.1em;
  }
    